import { string, object } from 'yup';

const excludeCyrillicSymbols = new RegExp(/^([^--]*)$/);

export const autorizationSchema = object()
  .shape({
    email: string().required().min(5),
    password: string()
      .min(8)
      .matches(excludeCyrillicSymbols, '    ')
      .required('  '),
  })
  .required();
